/* header
------------------------------------------------------ */
@use "../base/setting" as *;
@use "../global" as *;

.header {
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 64px;
  padding: 0 $padding-side;
  transition: all 0.3s;

  @include z-index(header);

  @include mq(md) {
    height: 86px;
    padding: 0 30px;
  }
}

.header--active {
  height: 64px;
  background-color: $c_key2;
}

.header-nav {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  visibility: hidden; //display:noneはtransitionが効かない
  background-image: linear-gradient(135deg, #7633a4, #250d65);
  opacity: 0;
  transition: opacity 0.5s;

  @include z-index(header);

  @include mq(md) {
    position: static;
    width: auto;
    height: auto;
    visibility: visible;
    background-image: none;
    opacity: 1;
  }
}

.header-nav.fade {
  visibility: visible;
  opacity: 1;
}

.header-nav__list {
  position: absolute;
  top: 50%;
  left: 50%;
  font-size: rem(16);
  text-align: center;
  transform: translate(-50%, -50%);

  @include z-index(header);

  @include mq(md) {
    position: static;
    display: flex;
    font-size: rem(12);
    transform: none;
  }

  @include mq(lg) {
    font-size: rem(14);
  }
}

.header-nav__item {
  margin-bottom: 20px;
  font-weight: bold;
  text-transform: uppercase;

  @include mq(md) {
    margin-bottom: 0;
    margin-left: 25px;
  }
}

.header-nav__link {
  position: relative;
  display: inline-block;
  color: $c_white;
}

.header-nav__link::after {
  display: none;

  @include mq(md) {
    position: absolute;
    bottom: -5px;
    left: 0;
    display: block;
    width: 0;
    height: 1px;
    content: "";
    background-color: #fff;
    transition: width 0.3s;
  }
}

.header-nav__link:hover {
  text-decoration: none;
}

.header-nav__link:hover::after {
  width: 100%;
}
