/* media
------------------------------------------------------ */
@use "../base/setting" as *;
@use "../global/" as *;

.media {
  display: flex;
  flex-direction: column;

  @include mq(md) {
    flex-direction: row;
  }
}

.media:not(:last-of-type) {
  padding-bottom: 40px;

  @include mq(md) {
    padding-bottom: 60px;
  }
}

.media__img-wrapper {
  margin-right: 0;
  margin-bottom: 20px;

  @include mq(md) {
    flex: 0 1 40%;
    margin-right: 50px;
    margin-bottom: 0;
  }
}

.media__img {
  width: 100%;
}

.media__body {
  flex: 1;
}

.media__title {
  margin-bottom: 15px;
  font-size: rem(18);
  line-height: 1.2;
  text-align: center;

  @include mq(md) {
    margin-bottom: 20px;
    font-size: rem(24);
    line-height: normal;
    text-align: start;
  }
}

.media__text--br:not(:last-of-type) {
  margin-bottom: 20px;
}
