/* btn
------------------------------------------------------ */
@use "../base/setting"as *;
@use "../global/"as *;

.btn {
  position: relative;
  display: inline-block;
  width: 220px;
  max-width: 100%;
  padding: 15px 10px;
  font-size: rem(14);
  font-weight: bold;
  color: $c_white;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  background-image: $btn-gradient;
  background-size: 200% auto;
  border-radius: 50px;
}

.btn::after {
  position: absolute;
  right: 1.5em;
  font-family: "Font Awesome 5 Free";
  font-size: rem(15);
  font-weight: 900;
  content: "\f0da";
  transition: 0.3s;
}

.btn:hover {
  text-decoration: none;
  background-position: right center;
}

.btn:hover::after {
  right: 1.2em;
}

.btn--transparent {
  background: none;
  border: 1px solid $c_white;
}
