/* news
------------------------------------------------------ */
@use "../../../base/setting" as *;
@use "../../../global" as *;

.news-list__item {
  @include mq(md) {
    display: flex;
    align-items: flex-start;
    font-size: rem(14);
  }
}

.news-list__item:not(:last-child) {
  margin-bottom: 30px;

  @include mq(md) {
    margin-bottom: 15px;
  }
}

.news-list__header {
  display: flex;
}

.news-list__date {
  margin-right: 20px;
}

.news-list__tag {
  display: inline-block;
  width: 80px;
  padding: 2px 10px;
  margin-right: 20px;
  font-size: rem(12);
  font-weight: bold;
  text-align: center;
  background-color: #e8e8e8;
  border-radius: 50px;
}

.news-list__link {
  color: $c_text;
}
