/* pager
------------------------------------------------------ */
@use "../base/setting" as *;
@use "../global" as *;

.pager {
  display: flex;
  overflow-x: auto;

  &__inner {
    display: flex;
    margin-right: auto;
    margin-left: auto;
  }

  &__inner > *:last-child {
    margin-right: 0;
  }

  &__item {
    margin-right: 10px;
    list-style: none;
  }

  &__link {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 45px;
    height: 45px;
    font-size: rem(20);
    font-weight: bold;
    color: $c_white;
    text-decoration: none;
    background-image: linear-gradient(135deg, #7633a4, #250d65);
    border-radius: 50%;
    transition: 0.25s;
  }

  &__link--active {
    color: $c_text;
    pointer-events: none;
    background: #f1ebf8;
  }
}
