/* table
------------------------------------------------------ */
@use "../base/setting" as *;
@use "../global" as *;

.table__inner {
  width: 100%;
  border-collapse: collapse;  /* セル同士の間に隙間を開けない */
}

.table__row {
  border-bottom: 1px solid #ddd;

  @include mq(md) {
    display: flex;
    border-bottom: none;
  }
}

.table__header {
  display: block;
  width: 20%;
  margin-top: 20px;
  text-align: left;
  vertical-align: middle;

  @include mq(md) {
    padding: 30px 10px;
    margin-top: 0;
    border-bottom: 1px solid #ddd;
  }
}

.table__text {
  display: block;
  margin-bottom: 20px;
  text-align: left;

  @include mq(md) {
    flex: 1;
    padding: 30px 10px;
    margin-bottom: 0;
    border-bottom: 1px solid #ddd;
  }
}

.table__list {
  margin-top: 10px;
}

.table__item {
  list-style-position: inside;
  list-style-type: disc;
}
