/* works
------------------------------------------------------ */
@use "../../../base/setting"as *;
@use "../../../global"as *;

.works {
  padding: 0;
}

.works__inner {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 1300px;
  padding: 30px 20px 60px;

  @include mq(md) {
    flex-direction: row;
    padding: 60px 20px;
  }
}

//位置ずらし背景グレー
.works__inner::after {
  position: absolute;
  top: 31%;
  right: 0;
  z-index: -1;
  display: block;
  width: 100%;
  height: 68%;
  content: "";
  background-color: $c_gray1;

  @include mq(md) {
    top: 50%;
    right: 0;
    width: 60%;
    height: 100%;
    transform: translateY(-50%);
  }
}

.works__img-wrapper {
  width: 100%;
  margin-right: 0;
  margin-bottom: 20px;

  @include mq(md) {
    width: 59.5238095238%; // 画像サイズの幅をレイアウト幅で割る
    margin-right: 3.33333%; // 画像間の空白をレイアウト幅で割る
    margin-bottom: 0;
  }
}

.works__body {
  flex: 1;
  text-align: center;
}

.works__text {
  margin-bottom: 30px;
}
