/* contact
------------------------------------------------------ */
@use "../base/setting"as *;
@use "../global/"as *;

.contact {
  padding: 60px 0;
  margin-top: 40px;
  text-align: center;
  background-color: $c_gray1;

  @include mq(md) {
    padding: 80px 0;
  }
}

.contact__text {
  margin-bottom: 20px;

  @include mq(md) {
    font-size: rem(15);
  }
}

//タブレット幅以上では改行なし
.contact__text-br {
  @include mq(md) {
    display: none;
  }
}
