/* mv
------------------------------------------------------ */
@use "../../../base/setting" as *;
@use "../../../global" as *;

.mv {
  height: 100vh;
  color: $c_white;
  background-image: url(../../images/common/hero.png);
  background-position: center;
  background-size: cover;

  @include flex-center;
}

.mv__inner {
  padding: 0 30px;
  text-align: left;

  @include mq(md) {
    text-align: center;
  }
}

.mv__title-main {
  margin-bottom: 20px;
  font-size: rem(40);
  line-height: 1.1;

  @include mq(md) {
    font-size: rem(72);
  }
}

.mv__title-sub {
  margin-bottom: 30px;
  font-size: rem(16);
  letter-spacing: 0.1em;

  @include mq(md) {
    margin-bottom: 50px;
    font-size: rem(24);
  }
}

.mv__title-sub-br {
  @include mq(md) {
    display: none;
  }
}

.mv__scrolldown {
  position: absolute;
  right: -10px;
  bottom: 85px;
  font-size: rem(12);
  line-height: 1;
  color: #fff;
  text-transform: uppercase;
  letter-spacing: 0.2em;

  @include mq(md) {
    right: 3px;
    bottom: 115px;
  }
}

.mv__scrolldown-text {
  display: inline-block;
  transform: rotate(90deg);
}

.mv__scrolldown::after {
  position: absolute;
  bottom: -115px;
  left: 50%;
  width: 1px;
  height: 80px;
  content: "";
  background-color: $c_white;
  animation: scrollDownLine 1.5s infinite;
  animation: scrollDownLine 1.5s infinite;
}

@keyframes scrollDownLine {
  0% {
    transform: scale(1, 0);
    transform: scale(1, 0);
    transform-origin: 0 0;
    transform-origin: 0 0;
  }

  50% {
    transform: scale(1, 1);
    transform: scale(1, 1);
    transform-origin: 0 0;
    transform-origin: 0 0;
  }

  50.1% {
    transform: scale(1, 1);
    transform: scale(1, 1);
    transform-origin: 0 100%;
    transform-origin: 0 100%;
  }

  100% {
    transform: scale(1, 0);
    transform: scale(1, 0);
    transform-origin: 0 100%;
    transform-origin: 0 100%;
  }
}

@keyframes scrollDownLine {
  0% {
    transform: scale(1, 0);
    transform: scale(1, 0);
    transform-origin: 0 0;
    transform-origin: 0 0;
  }

  50% {
    transform: scale(1, 1);
    transform: scale(1, 1);
    transform-origin: 0 0;
    transform-origin: 0 0;
  }

  50.1% {
    transform: scale(1, 1);
    transform: scale(1, 1);
    transform-origin: 0 100%;
    transform-origin: 0 100%;
  }

  100% {
    transform: scale(1, 0);
    transform: scale(1, 0);
    transform-origin: 0 100%;
    transform-origin: 0 100%;
  }
}
