/* news-pager
------------------------------------------------------ */
@use "../../../base/setting"as *;
@use "../../../global"as *;

.news-pager {
  padding-bottom: 20px;
}

.news-pager__inner {
  display: flex;
  margin-right: auto;
  margin-left: auto;
  border-top: 1px solid #b8aed5;
  border-bottom: 1px solid #b8aed5;
}

.news-pager__item {
  flex: 1;
  margin: 15px 0;
}

.news-pager__item:hover {
  background-color: #f9f7ff;
}

.news-pager__item:not(:last-child) {
  border-right: 1px solid #b8aed5;
}

.news-pager__link {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px 20px;
  font-size: rem(12);

  @include mq(md) {
    font-size: rem(14);
  }
}

.news-pager__link:hover {
  text-decoration: none;
}

.news-pager__home {
  text-align: center;
}
