/* recruit-job
------------------------------------------------------ */
@use "../../../base/setting"as *;
@use "../../../global"as *;

.recruit-job__list {
  padding: 30px 20px;
  background-color: #fff;
  border: 1px solid #b067cf;
  border-radius: 6px;

  @include mq(md) {
    padding: 60px 95px;
  }
}

.recruit-job__name {
  margin-bottom: 20px;
  font-size: rem(16);
  text-align: center;

  @include mq(md) {
    margin-bottom: 30px;
    font-size: rem(24);
  }
}

.recruit-job__table {
  margin-bottom: 40px;

  @include mq(md) {
    margin-bottom: 50px;
  }
}

.recruit-job__link {
  text-align: center;
}

.recruit-job__btn {
  margin-bottom: 5px;
}

.recruit-job__note {
  font-size: rem(12);
}
