/* message
------------------------------------------------------ */
@use "../base/setting"as *;
@use "../global/"as *;

.message {
  text-align: center;
}

.message__underline {
  background: linear-gradient(transparent 70%, #f5f534 0%);
}

.message__heading {
  display: inline-block;
  margin-bottom: 40px;
  font-size: rem(32);
  font-weight: bold;
  line-height: 1.5;

  @include mq(md) {
    font-size: rem(40);
    line-height: 1;
  }
}

.message__heading-br {
  @include mq(md) {
    display: none;
  }
}

.message__text {
  margin-bottom: 30px;
}

.message__text-br {
  @include mq(md) {
    display: none;
  }
}
