// ============================================================
// 設定
// ============================================================
@use "../global/" as *;

// コンテナーの左右余白
$padding-side: 15px;

// $padding-side-sp: 15px;

// ページの全体の横幅（大）
$width_lg: 1300px;

// ページの全体の横幅（標準）
$width: 1140px;

// ページの全体の横幅（中間）
$width_md: 950px;

// ページの全体の横幅（狭い）
$width_sm: 780px;

// レイアウト幅
// $layout-width : $width + $padding-side * 2;

//標準フォントサイズ(sp)
//$fz_pc: 14px;

//標準フォントサイズ(pc)
//$fz_pc: 16px;

// 標準font-family
// $font_serif : 'Shippori Mincho', serif;
// $font_sans : 'Noto Sans JP', sans-serif;

//欧文font-family
// $font_heebo : 'Heebo', sans-serif;
// $font_roboto : 'Roboto', sans-serif;
// $font_bebas : 'Bebas Neue', cursive;

//白黒
$c_white: #fff;
$c_black: #000;

//グレー
$c_gray1: #f0f0f0;

//キーカラー
$c_key1: #391e88;
$c_key2: #21054d;
$c_key3: #393636;

// 標準テキストカラー
$c_text: #333;

// ボタンのグラデーションカラー
$btn-gradient: linear-gradient(to right, #7633a4 0%, #250d65 51%, #7633a4 100%);

// z-index
$layer: (
  hamburger: 100,
  header: 20,
  page-top: 10,
  badge: 2,
  default: 1,
);

@mixin z-index($key) {
  z-index: map-get($layer, $key);
}
