/* recruit-message
------------------------------------------------------ */
@use "../../../base/setting" as *;
@use "../../../global" as *;

.recruit-message {
  overflow: hidden;
}

.recruit-message__heading {
  margin: 20px 0;
  color: $c_key1;
  text-align: center;
}

.recruit-message__title {
  margin-bottom: 10px;
  font-size: rem(48);
  line-height: 1.2;

  @include mq(md) {
    font-size: rem(72);
    line-height: 1;
  }
}

.recruit-message__sub-title {
  font-size: rem(18);

  @include mq(md) {
    font-size: rem(32);
  }
}

.recruit-message__lead {
  margin-bottom: 40px;
  font-size: rem(15);
  text-align: center;

  @include mq(md) {
    font-size: rem(16);
  }
}

.recruit-message__img-wrapper {
  width: 100vw;
  margin: 0 calc(50% - 50vw);
}
