/* news-article
------------------------------------------------------ */
@use "../../../base/setting"as *;
@use "../../../global"as *;

.news-article__inner {
  max-width: 750px;
}

.news-article__body {
  margin-bottom: 45px;
}

.news-article__head {
  margin-bottom: 20px;

  @include mq(md) {
    margin-bottom: 30px;
  }
}

.news-article__title {
  margin-bottom: 10px;
  font-size: rem(22);
  font-weight: normal;
  line-height: 1.5;

  @include mq(md) {
    font-size: rem(32);
  }
}

.news-article__img-wrapper {
  margin-bottom: 30px;
}

.news-article__text {
  font-size: rem(15);

  @include mq(md) {
    font-size: rem(16);
  }
}

.news-article__text:not(:last-of-type) {
  margin-bottom: 30px;
}

.news-article__heading-lv3 {
  position: relative;
  padding-left: 20px;
  margin-top: 40px;
  margin-bottom: 20px;
  font-size: rem(18);
  font-weight: bold;
  line-height: 1.6;

  @include mq(md) {
    margin-top: 60px;
    font-size: rem(24);
  }
}

.news-article__heading-lv3::before {
  position: absolute;
  top: 0;
  left: 0;
  width: 6px;
  height: 100%;
  content: "";
  background-color: $c_key1;
  border-radius: 3px;
}

.news-article__heading-lv4 {
  margin-top: 40px;
  margin-bottom: 20px;
  font-size: rem(17);
  font-weight: bold;

  @include mq(md) {
    font-size: rem(18);
  }
}

.news-article__sns {
  margin-bottom: 40px;
}
