/* breadcrumb
------------------------------------------------------ */
@use "../base/setting"as *;
@use "../global/"as *;

.breadcrumb {
  position: sticky;
  top: 64px;
  background-color: $c_gray1;
}

.breadcrumb__list {
  max-width: $width;
  padding: 5px 15px;
  margin: 0 auto;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;

  @include mq(md) {
    padding: 10px 15px;
  }
}

.breadcrumb__item {
  display: inline;
  font-size: rem(11);
  text-transform: uppercase;

  @include mq(md) {
    font-size: rem(12);
  }
}

.breadcrumb__item:not(:last-child)::after {
  margin: 0 0.5em;
  content: ">";
}
