/* footer
------------------------------------------------------ */
@use "../base/setting" as *;
@use "../global/" as *;

.footer {
  background-color: $c_black;
}

.footer__inner {
  padding: 60px 30px;
  text-align: center;

  & .copyright {
    color: white;
  }
}

.footer-nav {
  padding-bottom: 30px;
}

.footer-nav__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;

  @include mq(md) {
    justify-content: center;
  }
}

.footer-nav__item:not(:last-child) {
  margin-right: 10px;
}

.footer-nav__link {
  padding: 5px;
  font-size: rem(12);
  color: $c_white;
  text-transform: uppercase;
  letter-spacing: 0.1em;
}

.footer-copyright {
  font-size: rem(10);
  text-transform: uppercase;
}

.footer-copyright__link {
  color: #a798b7;
}
