/* contact-form
------------------------------------------------------ */
@use "../../../base/setting" as *;
@use "../../../global" as *;

.contact-form__inner {
  max-width: 780px;
}

.contact-form__lead {
  margin-bottom: 40px;
  text-align: center;
}

.contact-form__text {
  font-size: rem(14);
  font-weight: normal;

  @include mq(md) {
    font-size: rem(15);
  }
}

.contact-form__body {
  padding: 30px;
  background-color: #f7f7f7;

  @include mq(md) {
    padding: 90px;
  }
}
