/* ----------------------------------------
	共通部分
---------------------------------------- */
@use "setting"as *;
@use "../global/" as *;

html {
  box-sizing: border-box;
  font-size: 100%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: Montserrat, Arial, "Hiragino Kaku Gothic ProN", "Hiragino Sans", Meiryo, sans-serif;
  font-size: rem(14);
  line-height: 1.8;
  color: $c_text;
  letter-spacing: 0.05em;

  @include mq(md) {
    font-size: rem(15);
  }
}

a {
  color: $c_key1;
  text-decoration: none;
  transition: 0.3s;

  &:hover {
    text-decoration: underline;
  }
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: top;
}

li {
  list-style: none;
}
