/* heading-lv2
------------------------------------------------------ */
@use "../base/setting"as *;
@use "../global/"as *;

.heading-lv2 {
  margin-bottom: 30px;
  font-weight: bold;
  line-height: 1.2;
  color: $c_key1;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 0.1em;
}

.heading-lv2__main {
  margin-bottom: 5px;
  font-size: rem(30);

  @include mq(md) {
    font-size: rem(36);
  }
}

.heading-lv2__sub {
  font-size: rem(13);
}

.heading-lv2--white {
  color: $c_white;
}
